@font-face {
  font-family: 'Walkway';
  src: url('/fonts/Walkway-UltraBold.ttf') format('truetype');
}

@font-face {
  font-family: 'Ruda';
  src: url('/fonts/Ruda-Regular.ttf') format('truetype');
}

* {
  margin: 0;
  padding: 0;
}

@media screen and (orientation: portrait) {
  .app {
    display: none;
  }
}

#title-area {
  position: absolute;
  top: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 50%;
  background-color: #6f8a91;
  color: #ececec;
  text-align: center;
  -webkit-transition: background-color 1.5s;
  -o-transition: background-color 1.5s;
  transition: background-color 1.5s;
}

#title-area td {
  padding-bottom: 2vh;
}

#title {
  font-family: 'Walkway', sans-serif;
  font-size: 6vw;
}

#subtitle {
  font-family: 'Ruda', sans-serif;
  font-size: 2vw;
}

#interact-area {
  position: absolute;
  top: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 50%;
  background: #ececec;
  color: #333333;
  text-align: center;
}

#interact-area th {
  padding-bottom: 2vh;
}

#interact-area td {
  padding-bottom: 1vh;
}

#play-btn img {
  height: 25vh;
  width: auto;
}

#play-btn img:hover {
  cursor: pointer;
  -webkit-filter: drop-shadow(0 0 6px #333333);
          filter: drop-shadow(0 0 6px #333333);
}

.label {
  font-family: 'Ruda', sans-serif;
  font-size: 2.5vw;
}

#select-title {
  font-family: 'Ruda', sans-serif;
  font-size: 3vw;
  font-weight: normal;
}

.clan-icon {
  width: 20vw;
}

.clan-icon img {
  max-width: 12vw;
  max-height: 20vh;
}

#title-backdrop {
  position: absolute;
  top: 0;
  width: 100%;
  height: 50%;
  z-index: -1;
}

#goat-clan:not(.selected):hover {
  -webkit-filter: brightness(0) saturate(100%) invert(66%) sepia(91%) saturate(3167%) hue-rotate(341deg) brightness(102%) contrast(101%);
          filter: brightness(0) saturate(100%) invert(66%) sepia(91%) saturate(3167%) hue-rotate(341deg) brightness(102%) contrast(101%);
}

#bison-clan:not(.selected):hover {
  -webkit-filter: brightness(0) saturate(100%) invert(64%) sepia(13%) saturate(2315%) hue-rotate(91deg) brightness(78%) contrast(80%);
          filter: brightness(0) saturate(100%) invert(64%) sepia(13%) saturate(2315%) hue-rotate(91deg) brightness(78%) contrast(80%);
}

#fish-clan:not(.selected):hover {
  -webkit-filter: brightness(0) saturate(100%) invert(66%) sepia(14%) saturate(3686%) hue-rotate(165deg) brightness(98%) contrast(91%);
          filter: brightness(0) saturate(100%) invert(66%) sepia(14%) saturate(3686%) hue-rotate(165deg) brightness(98%) contrast(91%);
}

.clan-icon img:hover {
  cursor: pointer;
}

.clan-icon img.selected {
  -webkit-filter: brightness(0) saturate(100%) invert(56%) sepia(71%) saturate(0%) hue-rotate(167deg) brightness(94%) contrast(92%);
          filter: brightness(0) saturate(100%) invert(56%) sepia(71%) saturate(0%) hue-rotate(167deg) brightness(94%) contrast(92%);
}

.trans {
  background-color: transparent !important;
}

.mountains {
  background: #ff7f2a url('/img/mountains.svg');
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: center;
}

.plains {
  background: #2ca05a url('/img/plains.svg');
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: center;
}

.coastal {
  background: #33b8ee url('/img/coastal.svg');
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: center;
}

#play-header {
  position: absolute;
  top: 0;
  width: 100%;
  height: 15vh;
  background-color: #6f8a91;
  color: #ececec;
}

#play-header td:not(.play-header-icon) {
  width: 60%;
  text-align: center;
}

#play-header td.play-header-icon {
  width: 20%;
}

#play-title {
  font-family: 'Walkway', sans-serif;
  font-size: 4vw;
}

.play-header-icon img {
  max-width: 4vw;
  max-height: 8vh;
}

.play-header-icon img:hover {
  cursor: pointer;
  -webkit-filter: brightness(0) saturate(100%) invert(97%) sepia(0%) saturate(12%) hue-rotate(162deg) brightness(88%) contrast(85%);
          filter: brightness(0) saturate(100%) invert(97%) sepia(0%) saturate(12%) hue-rotate(162deg) brightness(88%) contrast(85%);
}

.play-header-icon.left img {
  float: left;
  margin-left: 2vw;
}

.play-header-icon.right img {
  float: right;
  margin-right: 2vw;
}

#play-pointer {
  position: absolute;
  bottom: 35vh;
  z-index: 1;
  width: 6vw;
  height: auto;
}

#play-pointer.clickable:hover {
  cursor: pointer;
}

#play-area {
  position: absolute;
  top: 15vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  height: 85vh;
  overflow: hidden;
  background-color: #ececec;
  color: #333333;
}

#play-board {
  position: absolute;
  top: 45vh;
  width: 60vw;
  height: auto;
  -webkit-transition: -webkit-transform 2s;
  transition: -webkit-transform 2s;
  -o-transition: transform 2s;
  transition: transform 2s;
  transition: transform 2s, -webkit-transform 2s;
}

#play-info {
  position: absolute;
  top: 4vh;
  width: 90vw;
}

#play-info th {
  font-family: 'Ruda', sans-serif;
  font-size: 2.5vw;
  font-weight: normal;
}

#play-info td {
  font-family: 'Walkway', sans-serif;
  font-size: 2.5vw;
  text-align: center;
  color: #333333;
}

.player {
  display: inline-block;
  width: 3vw;
  height: 3vw;
  line-height: 3vw;
  font-family: 'Ruda', sans-serif;
  font-size: 1.5vw;
  color: #ececec;
  background-color: #999999;
  border-radius: 50%;
}

.player:hover {
  cursor: pointer;
}

.player:not(:last-child) {
  margin-right: 1vw;
}

.player.current {
  background-color: #333333;
}

#goat-player:hover {
  background-color: #ff7f2a;
}

#bison-player:hover {
  background-color: #2ca05a;
}

#fish-player:hover {
  background-color: #33b8ee;
}

#myths {
  height: 3.5vw;
}

.myth {
  max-width: 4vw;
  max-height: 3.5vw;
}

.myth:hover {
  cursor: pointer;
}

.myth:not(:last-child) {
  margin-right: 1vw;
}

.myth:not(.current) {
  -webkit-filter: brightness(0) saturate(100%) invert(56%) sepia(71%) saturate(0%) hue-rotate(167deg) brightness(94%) contrast(92%);
          filter: brightness(0) saturate(100%) invert(56%) sepia(71%) saturate(0%) hue-rotate(167deg) brightness(94%) contrast(92%);
}

.resource:not(:last-child) {
  margin-right: 1vw;
}

.resource:not(.current) {
  color: #999999;
}

.modal {
  position: fixed;
  z-index: 2;
  padding-top: 20vh;
  left: 0;
  top: 15vh;
  width: 100%;
  height: 85vh;
  overflow: auto;
  background-color: rgb(51,51,51);
  background-color: rgba(51, 51, 51, 0.4);
}

.modal-content {
  background-color: #ececec;
  margin: auto;
  width: 40vw;
  height: 40vh;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
}

.modal-header {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 8vh;
  line-height: 8vh;
  vertical-align: middle;
  font-family: 'Ruda', sans-serif;
  font-size: 2.5vw;
  text-align: center;
  color: #333333;
}

.modal-header.storyteller {
  background-color: #9247bd;
}

.modal-header.scenario {
  background-color: #ffcc00;
}

.modal-header.goat-resource {
  background-color: #ff7f2a;
}

.modal-header.bison-resource {
  background-color: #2ca05a;
}

.modal-header.fish-resource {
  background-color: #33b8ee;
}

.modal-data {
  position: relative;
  width: 100%;
  height: 32vh;
}

.modal-message, .modal-result {
  text-align: center;
  font-family: 'Ruda', sans-serif;
  font-size: 1.25vw;
}

.modal-message {
  padding: 3vw 2vw;
}

.modal-result {
  padding: 0 2vw;
}

.modal-btn {
  position: absolute;
  bottom: 0;
  clear: both;
  padding-bottom: 1vw;
  width: 100%;
  font-family: 'Walkway', sans-serif;
  font-size: 1.75vw;
  text-align: center;
  color: #333333;
}

.modal-btn span:hover {
  cursor: pointer;
  color: #999999;
}

#play-message {
  position: absolute;
  top: 28vh;
  font-family: 'Ruda', sans-serif;
  font-size: 2.5vw;
  text-align: center;
}



